@import url(https://fonts.googleapis.com/css?family=Khula:700);

.hidden {
  opacity:0;
}

.console-container {
 
  font-family:Khula;
  font-size:4em;
  text-align:left;
  height:200px;
  width:600px;
  display:block;
  position:relative;
  color:white;
  top:0;
  bottom:0;
  left:0;
  right:0;
  margin-right: 70%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.projectIframe {
  background-color: #282c34;
}

.navBanner {
  position: absolute;
  z-index: -100;
  display: flex;
  width: 100vw; 
  align-items: center;
  justify-content: center;
}

#wrapper {
    position: relative;
}

.navBanner img { 
  border-bottom-right-radius: 50px;
  border-bottom-left-radius: 50px;
} 

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
